html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  text-rendering: optimizeLegibility; }

html, body {
  height: 100%;
  margin: 0; }

html {
  overflow-y: scroll; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

.logo {
  cursor: pointer;
  position: absolute;
  width: 240px;
  height: 110px; }
  .logo img {
    position: absolute;
    width: 100%;
    height: 100%;
    -o-object-position: 0 -6px;
       object-position: 0 -6px;
    -o-object-fit: cover;
       object-fit: cover; }
  .logo span {
    position: absolute;
    color: #fff;
    z-index: 1;
    width: 100%;
    bottom: 0;
    line-height: 36px;
    text-align: center;
    text-decoration: underline; }

.logo:hover {
  font-weight: bold; }

.navtop {
  border-top: 30px solid #37f;
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  background: #eee;
  border-bottom: 5px solid #eee; }
  .navtop .fill {
    width: 240px;
    height: 80px; }
  .navtop a {
    line-height: 80px;
    padding: 0 16px;
    color: #37f;
    font-weight: bold;
    font-size: 30px;
    text-decoration: none; }
    .navtop a:hover {
      text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3); }
  .navtop a.ham {
    padding: 0;
    width: 60px;
    width: calc(100% - 240px);
    max-width: 100px;
    text-align: center;
    color: #000;
    display: none; }
  .navtop a.link {
    background: #fff;
    color: #000;
    font-weight: normal;
    white-space: nowrap;
    text-align: center;
    padding: 22px;
    font-size: 14px;
    line-height: 18px;
    border-right: 1px solid #ddd; }
    .navtop a.link.oneline {
      padding: 31px 22px; }
  .navtop a.tel {
    -webkit-box-flex: 1;
            flex-grow: 1;
    text-align: right; }

@media (max-width: 1180px) {
  .navtop a.ham {
    display: block; }
  .navtop a.link {
    display: block;
    overflow: hidden;
    flex-basis: 100%;
    height: 0px;
    line-height: 14px;
    padding: 0;
    border: 0 solid #fff;
    position: relative;
    color: #fff;
    border-right: none;
    -webkit-box-ordinal-group: 5;
            order: 4; }
    .navtop a.link.oneline {
      padding: 0; }
    .navtop a.link br {
      display: none; }
  .navtop.animateHeight a.link {
    -webkit-transition: padding .25s;
    transition: padding .25s; }
  .navtop a.tel {
    -webkit-box-ordinal-group: 4;
            order: 3; }
  .navtop.expand a.link {
    color: #000;
    border-bottom: 1px solid #ddd;
    height: inherit;
    padding: 13px 0 9px 0; }
    .navtop.expand a.link:hover {
      background: #ddf;
      text-shadow: none; } }
